<template>
  <div class="list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">设备维护</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <div class="filter-select mr-12">
             <StatusSelect placeholder="选择设备类型"
                          :filterable ='true'
                          :type = 1
                          v-model="search.type"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备名称"
                          :filterable ='true'
                          :type = 2
                          v-model="search.name"></StatusSelect>
          </div>
          <div class="filter-select mr-12">
            <StatusSelect placeholder="选择设备编号"
                          :filterable ='true'
                          :type = 3
                          v-model="search.code"></StatusSelect>
          </div>
          <div class="filter-btn">
            <Button type="default"
                    @click="loadTableData()"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
        <div class="filter-search-right">
          <span class="add-btn">
            <Button type="default"
                    @click="showDrawer('add')">
              <span class="iconfont icon-a-lianhe68"></span>
              新增
            </Button>
          </span>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
      <custom-drawer ref="drawer"
                     width="26%"
                     :title="title"
                     :formValidate="formValidate"
                     :formList="formList"
                     v-model="formValue"
                     @selectTree="selectTree"
                     @getlocation="getlocation"
                     :selectLocation="selectLocation"
                     @confirm="confirm"></custom-drawer>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import {deviceList, updateDeviceList, deleteDeviceList, addDeviceList} from '@/api/work'
import {getDeviceSearchList, getDeviceTypeList, deviceModel, getParentInfo, getTreeList} from '@/api/device'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        type: '',
        name: '',
        code: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '设备名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '设备类型',
          key: 'typeName',
          align: 'left'
        },
        {
          title: '设备型号',
          key: 'modelName',
          align: 'left'
        },
        {
          title: '设备编码',
          key: 'deviceCode',
          align: 'left'
        },
        {
          title: '坐标',
          key: 'latitude',
          align: 'left',
          width: 200,
          render: (h, params) => {
            if (params.row.latitude && params.row.longitude) {
              return h('div', params.row.longitude + ',' + params.row.latitude)
            } else {
              return ''
            }
          }
        },
        // {
        //   title: '制造商',
        //   key: 'manufactruer',
        //   align: 'left',
        //   width: 200
        // },
        {
          title: '控制场景',
          key: 'deviceControlMode',
          align: 'left',
          render: (h, params) => {
            if (params.row.deviceControlMode) {
              switch (params.row.deviceControlMode) {
                case 'SINGLE_FCU':
                  return h('div', '单机FCU控制')
                case 'ALL_FCU':
                  return h('div', '整机FCU控制')
                case 'PCS':
                  return h('div', 'PCS控制')
                case 'BATTERY':
                  return h('div', '电池控制')
              }
            } else {
              return h('div', '')
            }
          }
        },
        // {
        //   title: '配置文件',
        //   key: 'configurationFileName',
        //   align: 'left'
        // },
        // {
        //   title: '孪生模型',
        //   key: 'twinModel',
        //   align: 'left'
        // },
        // {
        //   title: '健康诊断模型',
        //   key: 'healthDiagnosisModel',
        //   align: 'left'
        // },
        {
          title: '操作',
          key: 'action',
          width: 200,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.goDetail(params.row)
                    }
                  }
                },
                '详情'
              ),
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#0084FF'
                  },
                  on: {
                    click: () => {
                      this.showDrawer('edit', params.row)
                    }
                  }
                },
                '编辑'
              ),
              h(
                'span',
                {
                  class: 'primary',
                  style: {
                    marginLeft: 0,
                    color: '#F12121'
                  },
                  on: {
                    click: () => {
                      this.showDrawer('delete', params.row)
                    }
                  }
                },
                '删除'
              )
            ])
          }
        }
      ],
      currentIndex: 0,
      title: '',
      formList: [
        {
          label: '设备名称',
          type: 'input',
          value: 'deviceName'
        },
        {
          label: '设备类型',
          type: 'select',
          value: 'deviceType',
          data: []
        },
        {
          label: '设备型号',
          type: 'select',
          value: 'deviceModel',
          data: []
        },
        {
          label: '设备编码',
          type: 'input',
          value: 'deviceCode',
          data: []
        },
        {
          label: '坐标',
          type: 'locationSelect',
          value: 'addrCode',
          placeholder: '请选择坐标'
        },
        {
          label: '父级设备',
          type: 'select',
          value: 'parentDeviceId',
          placeholder: '请选择父级设备',
          clearable: true,
          data: []
        },
        {
          label: '控制场景',
          value: 'deviceControlMode',
          type: 'radio',
          data: [
            {name: '单机FCU控制', value: 'SINGLE_FCU'},
            {name: '整机FCU控制', value: 'ALL_FCU'},
            {name: 'PCS控制', value: 'PCS'},
            {name: '电池控制', value: 'BATTERY'}
          ]
        },
        {
          label: '关联故障树',
          value: 'accidentTreeId',
          type: 'select',
          placeholder: '请选择关联故障树',
          data: [],
          clearable: true
        }
      ],
      formValue: {},
      formValidate: {
        deviceName: [{required: true, message: '设备名称不能为空'}],
        deviceType: [{required: true, message: '设备类型不能为空'}],
        deviceModel: [{required: true, message: '设备型号不能为空'}],
        deviceCode: [{required: true, message: '设备编码不能为空'}]
        // addrCode: [{required: true, message: '所属区域不能为空', type: String}]
        // deviceStatus: [{required: true, message: '请选择设备状态'}]
      },
      type: ''
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getlocation (e) {
      this.formValue.longitude = e.lng
      this.formValue.latitude = e.lat
    },
    getType () {
      getDeviceSearchList().then(res => {
        let { code, data } = res
        if (code === 200) {
          this.formList[0].data = data.map(i => {
            return {
              name: i.deviceName,
              value: i.deviceName
            }
          })
        }
      })
      getTreeList().then(res => {
        if (res.code === 200) {
          this.formList[7].data = res.data.map(i => {
            return {
              name: i.accidentTreeName,
              value: i.id
            }
          })
        }
      })
      getDeviceTypeList().then(res => {
        let { code, data } = res
        if (code === 200) {
          this.formList[1].data = data.map(i => {
            return {
              name: i.typeName,
              value: i.id
            }
          })
        }
      })
      deviceModel().then(res => {
        if (res.code === 200) {
          this.formList[2].data = res.data.map(i => {
            return {
              name: i.modelName,
              value: i.id
            }
          })
        }
      })
      getParentInfo().then(res => {
        if (res.code === 200) {
          this.formList[5].data = res.data.map(i => {
            return {
              name: i.deviceName,
              value: i.deviceId
            }
          })
        }
      })
    },
    loadTableData (clear) {
      let {pageSize, pageIndex: pageNumber} = this.pageInfo
      this.pageInfo.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      deviceList({pageSize, pageNumber, ...this.search}).then(res => {
        let {code, data} = res
        this.pageInfo.loading = false
        if (code === 200) {
          this.tableData = data.result
          this.pageInfo.totalElements = data.totalElements
        }
      })
    },
    goDetail (params) {
      this.$router.push({
        path: '/device-list-detail',
        query: {id: params.id, deviceName: params.deviceName, treeId: params.accidentTreeId || ''}
      })
    },
    showDrawer (type, params) {
      if (type !== 'delete') {
        if (type === 'add') {
          this.title = '新建设备'
          this.formValue = {}
          this.selectLocation = ''
          this.formValue.longitude = ''
          this.formValue.latitude = ''
        } else {
          let val = JSON.parse(JSON.stringify(params))
          this.title = '编辑设备'
          this.formValue = val
          this.formValue.deviceType = val.deviceTypeId
          this.formValue.parentDeviceId = Number(val.parentDeviceId)
          if (val.longitude && val.latitude) {
            this.selectLocation = val.longitude + ',' + val.latitude
          } else {
            this.selectLocation = ''
            this.formValue.longitude = ''
            this.formValue.latitude = ''
          }
        }
        this.type = type
        this.$refs.drawer.open()
      } else {
        this.$Modal.confirm({
          title: this.$t('common.text.prompt'),
          width: this.$flexible(450),
          content:
          this.$t('common.text.confirm_title', {
            name: '设备名称'
          }) + `[${params.deviceName}]的设备吗?`,
          onOk: () => {
            deleteDeviceList({id: params.id}).then(
              (res) => {
                if (res.code === 200) {
                  this.$Message.success(res.msg)
                  this.loadTableData()
                } else {
                  this.$Message.warning(res.msg)
                }
              },
              (res) => {
                this.$Message.warning(res.msg)
              }
            )
          }
        })
      }
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    },
    confirm () {
      console.log(this.formValue)
      this.formValue.state = this.formValue.deviceStatus
      if (this.type === 'edit') {
        updateDeviceList({...this.formValue}).then(res => {
          let {code, msg} = res
          if (code === 200) {
            this.$Message.success(this.$t('common.message.edit_success'))
            this.loadTableData()
            this.$refs.drawer.close()
            this.getType()
          } else {
            this.$Message.warning(msg)
          }
        })
      } else {
        addDeviceList({...this.formValue}).then(res => {
          let {code, msg} = res
          if (code === 200) {
            this.$Message.success(this.$t('common.message.add_success'))
            this.loadTableData()
            this.getType()
            this.$refs.drawer.close()
          } else {
            this.$Message.warning(msg)
          }
        })
      }
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  }
}
</script>

<style lang="less" scoped>
@import '../../../../style/varient';
@size-padding: 10px;

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



