export const getWorkList = ({pageNumber = 0, pageSize = 10, worderType, name, status}) => {
  const data = {
    pageNumber,
    pageSize,
    worderType,
    name,
    status
  }
  return axios.request({
    url: '/order/list',
    params: data,
    method: 'get'
  })
}

export const addWork = ({
  id,
  worderType,
  planIds,
  deviceId,
  maintainId,
  malfunctionId,
  name,
  templateId,
  fileIds,
  principal = [],
  executive = [],
  remark
}) => {
  const data = {
    id,
    worderType,
    planIds,
    deviceId,
    maintainId,
    malfunctionId,
    fileIds,
    name,
    templateId,
    principal: principal.join(','),
    executive: executive.join(','),
    remark
  }
  return axios.request({
    url: '/order/save',
    data,
    method: 'post'
  })
}

export const editWork = ({
  id,
  worderType,
  planIds,
  deviceId,
  malfunctionId,
  name,
  templateId,
  fileIds,
  principal = [],
  executive = [],
  remark
}) => {
  const data = {
    id,
    worderType,
    planIds,
    deviceId,
    malfunctionId,
    fileIds,
    name,
    templateId,
    principal: principal.join(','),
    executive: executive.join(','),
    remark
  }
  return axios.request({
    url: '/order/edit',
    data,
    method: 'post'
  })
}

