// 切换状态
export const changeStatus = ({
  id,
  userIds = [],
  description,
  reason,
  method,
  type,
  reports,
  isKnowledge
}) => {
  const data = {
    id,
    userIds: userIds.join(','),
    description,
    reason,
    method,
    type,
    reports,
    isKnowledge
  }
  return axios.request({
    url: '/order/changeStatus',
    data: data,
    method: 'post'
  })
}

